@import "variables.scss";

.hero {
  max-width: 1920px;
  background: url(../img/hero-back.png) no-repeat;

  &-back {
    background-color: rgba(0, 0, 0, 0.64);
  }

  &-container {
    display: flex;
    justify-content: space-between;
    max-width: 1170px;
    margin: 0 auto;
    padding-top: 105px;
    padding-bottom: 140px;

    @media (max-width: 1190px) {
      padding-left: 10px;
      padding-right: 10px;
      justify-content: center;
    }

    @media (max-width: 940px) {
      flex-direction: column;
      justify-content: center;
    }
  }

  &__greeting {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 600px;
  }

  &__title {
    font-size: 39px;
    color: #ffffff;
    font-weight: 900;
    line-height: 1.5;
  }

  &__description {
    margin-top: 20px;
    font-size: 20px;
    color: #ffffff;
  }

  &__form {
    max-width: 410px;
    padding: 45px 50px;
    background-color: #ffffff;
    border-radius: 5px;

    form {
      display: flex;
      flex-direction: column;
    }

    @media (max-width: 1190px) {
      margin-left: 40px;
    }
  }

  &__form-description {
    font-size: 12px;
  }

  &__email, &__phone {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
  }

  &__phone-title, &__email-title {
    font-size: 20px;
  }

  &__email-field, &__phone-field {
    max-width: 310px;
    margin-top: 10px;
    border: 2px solid #444444;
    font-size: 20px;
    padding: 10px 5px;
  }
}